.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(245, 239, 239);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-color: rgba(128, 128, 128, 0.795);
}

div#root{
  display: grid;
  grid-template-columns: 330px 330px 330px;
  grid-template-rows: 1fr 400px 1fr 1fr; 
}

/* DE Ã˜VERSTE KORT ROW 1 */
#travelCard{
  grid-row: 1;
  grid-column: 1;
}
#outdoorCard{
  grid-row: 1;
  grid-column: 2;
}
/* DE FORSKELLIGE KORT ROW 2 */
#deliveryCard {
  grid-row: 2;
}
#parcelCard{
  grid-row: 2;
}
#checkCard{
  grid-row: 2;
}


